.stars {
  display: flex;
  &__star {
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 0.5px;
    height: 16px;
    width: 16px;
  }
  
  &__star:not(:last-child) {
    margin-right: 4px;
  }
}

.stars--1 :nth-last-child(n+5),
.stars--2 :nth-last-child(n+4),
.stars--3 :nth-last-child(n+3),
.stars--4 :nth-last-child(n+2),
.stars--5 :nth-last-child(n+1) {
  background-image: url(../images/star-active.svg);
}
